const PersonForm = (props) => {
	const { namevalue, numbervalue, onSubmit, onNameChange, onNumberChange, reset} = props
	return (
		<form onSubmit={onSubmit}>
			<div>
				name: <input namevalue={namevalue} onChange={onNameChange} />
			</div>
			<div>
				number: <input numbervalue={numbervalue} onChange={onNumberChange} />
			</div>
			<div>
				<button type='submit' onClick={reset}>add</button>
			</div>
			<div>
				<button type='reset'>reset fields</button>
			</div>
		</form>
	);
};

export default PersonForm;
